@import url(https://fonts.googleapis.com/css?family=Raleway:400,500);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.fa-quote-left{
  margin-right: 10px;
  font-size: 1.5em;
}

.quote-div {
  font-family: 'Raleway', sans-serif;
  width: 90vw;
  color: #09d3ac;
  border: solid thin #FFFFFF;
  display:grid;
  padding: 25px;
  align-content: center;
  margin-bottom: 10px;
}

#quote-string{
  font-weight: bold;
}

#author{
  font-weight: normal;
}

/* #Quote-String {} */

.controls-div{
  font-size: 1.2em;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  align-content: center;
}

.controls-div > div:first-of-type > a{
  font-size: calc(30px + 2vmin);
}

#new-quote {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 40px;
  margin: 10px auto;
  background-color: #dee3ec;
  color: #0b0e0e;
  font-size: calc(10px + 0.9vmin);
  border-radius: 5px;
}

#share{
  width: 30%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  text-align: center;
}

#share a{
  color: #FFFFFF;
}

#new-quote:hover{
  color: #282c34;
  background-color: #09d2ac;
}

@media only screen and (min-width: 992px) {
  .quote-div{
    display: grid;
    align-items: center;
    min-height: 40vh;
    width: 40vw;
  }

  .quote-div > p {
    height: 100%;
    width: 100%;
  }

  .controls-div{
    grid-template-columns: repeat(2, 1fr);
  }

  #share{
    /* align-content: space-between; */
    align-items: center;
    width: 100%;
    margin: 0 auto;
  }

  
} 
.App {
  text-align: center;
  background-color: #282c34;
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #FFFFFF;
}
